/** Преобразует переданную строку в однородную маску для поля ввода, заменяя все буквенно-цифровые символы общим - "#"
 * @param {string} value строка, из которой будет создана маска
 * @returns {string} маска для поля ввода. Например, "mm/ss" станет "##/##", а "(000)" - "(###)"
 */
const createMask = (value: string): string => value.replace(/[0-9A-ZА-Я]/gi, '#');

export default createMask;
